}
.boxGradient {
	-webkit-text-shadow: 1px 1px 1px rgba(0, 0, 0, .2);
	text-shadow: 1px 1px 1px rgba(0, 0, 0, .2);
	border-radius: 3px;
	border: solid 1px #566783; // #394558;
	background: transparent;
	display: inline-block;
	box-shadow: inset 0 1px 4px lighten(#394558, 10%);
	#gradient > .vertical-four-colors(rgba(255,255,255,.6), rgba(155,155,155,.3), 55%, rgba(0,0,0,.3), 55.1%, rgba(0,0,0,.6));
}

.boxhilite {
	box-shadow: inset 0 1px 4px lighten(#394558, 10%), 0 0 8px lighten(#18526b, 15%);
	border-color: #62779d;
}

.indent{
	margin-left:16px;
}

