.wwt-webclient-wrapper {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	color: @wc-text-color;
	.vtop {
		vertical-align:top;
	}

	#WorldWideTelescopeControlHost, #WWTCanvas,  canvas {
		margin: 0;
		/*position: fixed;ri
		top: 0;left: 0;*/ 
		height: 100%; 
		width: 100%;
		z-index: 0;
	}
	.navbar-fixed-top {
		background:none;
		border:none;
		.navbar-toggle{
			float:right;
			margin:4px;
			i.fa{
				font-size:@font-size-large;
			}
		}
		.navbar-collapse{
			background-color:@wc-bg-color;
			border: solid 1px #566783;
			ul li{ 
				a{
					font-size:@font-size-large;
					font-weight:100;
				}
				a.btn {
					width: 85px;
					float: none;
					display: inline-block;
					box-sizing: border-box;
					width: 31%;
					font-size: 16px;
					font-weight:100;
					padding-top: 10px;
					padding-bottom: 8px;
					margin-bottom:5px;
					margin-right:4px;
				}
			}
		}
	}
	
	.radio label, .checkbox label{
		min-height:0;
		padding-left:0;
	}

	a {
		color: @wc-text-color; 
	}

		a:hover, a:focus, a:active {
			color: white;
			text-decoration: none;
		}
		.dropdown-menu > li > a, .dropdown-menu > li > a{
			color: @wc-text-color;
			border-radius:2px;
		}
		.dropdown-menu > li > a {
		  &:hover,
		  &:focus {
			border:solid 1px fade(#fdfeff,40%);
		  }
		}
		

	span.bc a {
		font-size: 12px;
	}

	input.input-sm.tiny {
		width: 55px;
	}

	canvas {
		-ms-touch-action: none;
	}

	.popover {
		.popover-title {
			display: none;
		}

		background-color: @wc-bg-color;
		border: solid 1px rgba(105,115,150,.7);
	}

		.popover.bottom {
			.arrow:after {
				border-bottom-color: rgba(105,115,150,.7);
			}
		}

		.popover.top {
			.arrow:after {
				border-top-color: rgba(105,115,150,.7);
			}
		}

	#researchMenu, #researchMenu li, #researchMenu li a {
		max-width: 300px;
	}

		#researchMenu li a {
			text-overflow: ellipsis;
			overflow: hidden;
		}

	ul.dropdown-menu {
		box-shadow: 0 0 2px rgb(121,158,225);
		background-color: rgba(25,30,43,.88);

		li {
			a {
				border: solid 1px transparent;
			}

				a:hover {
					border: solid 1px fade(@wc-text-color,20%);
				}
		}

		.dropdown-submenu {
			position: relative;
		}

			.dropdown-submenu > .dropdown-menu {
				top: 0;
				left: 100%;
				margin-top: -6px;
				margin-left: -1px;
				border-radius: 0 6px 6px 6px;
			}

			.dropdown-submenu:hover > .dropdown-menu {
				display: block;
			}

			.dropdown-submenu > a:after {
				display: block;
				content: " ";
				float: right;
				width: 0;
				height: 0;
				border-color: transparent;
				border-style: solid;
				border-width: 5px 0 5px 5px;
				border-left-color: #cccccc;
				margin-top: 5px;
				margin-right: -10px;
			}

			.dropdown-submenu:hover > a:after {
				border-left-color: #ffffff;
			}

			.dropdown-submenu.pull-left {
				float: none;
			}

				.dropdown-submenu.pull-left > .dropdown-menu {
					left: -100%;
					margin-left: 10px;
					border-radius: 6px 0 6px 6px;
				}
	}

	div.dropup ul#researchMenu li.dropdown-submenu ul.dropdown-menu {
		top: auto;
		bottom: 0;
		//height: 100%;
		li {
			display: block;
		}
	}

	div.finder-scope {
		width: 390px;
		height: 440px;
		position: absolute;
		top: -9999px;
		z-index: 4;
		//opacity:0;
		.moveable, div.property-panel {
			margin-top: 103px;
			width: 291px;
			//height: 361px;
			padding: 6px;

