			}
		}

		.buttons {
			position: absolute;
			bottom: 6px;

			.btn {
				width: 89px;
				margin-right: 1px;
				
			}
		}
	}

	div.property-panel {
		position: relative;
		top: 0;
		//height: 267px;
		width: 278px;
		margin: -3px -8px -8px -8px;
	}

	div.explore-panel {
		padding-left: 4px;
		max-height: 97px;
		height: 97px;
		//overflow: hidden;
	}

	div.explore-panel.expanded {
			height: 377px;
			max-height: 377px;
		}

	#ribbon {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		z-index: 1;
		height: 34px;
		-moz-box-sizing: border-box;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		padding-left: 100px;
		border-bottom: solid 1px black;
		overflow: hidden;
		#gradient > .vertical-four-colors(rgb(105,115,150), rgb(34,38,47), 50%, rgb(15,15,20), 50.1%, rgb(38,48,72));

		a.btn.pull-right {
			margin: 3px 16px 0 0;
		}

		ul.wwt-tabs {
			padding: 0;
			margin: 0;

			li {
				display: inline-block;
				list-style: none;
				//width: 100px;
				border-top-right-radius: 4px;
				border-top-left-radius: 4px;
				position: relative;
				top: 1px;
				border: solid 1px transparent;
				border-bottom: 0;
				-moz-box-sizing: border-box;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;

				div.outer {
					border-top-right-radius: 4px;
					border-top-left-radius: 4px;
					border: solid 1px transparent;
					border-bottom: 0;
					overflow: hidden;
					position: relative;


					a {
						text-align: center;
						display: block;
						color: white;
						height: 34px;
						position: relative;
						overflow: hidden;
						top: -4px;

						.label {
							border-top-right-radius: 4px;
							border-top-left-radius: 4px;
							border: solid 1px transparent;
							border-bottom: 0;
							overflow: hidden;
							margin: 4px 0 1px;
							padding: 3px 16px 2px;
							display: block;
							font-size: 14px;
							font-weight: 600;
							line-height: 1;
							//width: 96px;
							-webkit-text-shadow: 0 1px 1px #333;
							text-shadow: 0 1px 1px #333;
						}

						.menu {
							height: 9px;
							border: solid 1px transparent;
							-moz-box-sizing: border-box;
							-webkit-box-sizing: border-box;
							box-sizing: border-box;
							display: block;
							//width: 96px;
							i {
								visibility: hidden;
								color: white;
								position: relative;
								top: -6px;
								-webkit-text-shadow: 0 1px 1px #333;
								text-shadow: 0 1px 1px #333;
							}
						}
					}

						a:active, a:focus, a:hover, a:visited {
							text-decoration: none;
							color: white;
						}
				}
			}

				li.active {
					border: solid 1px #8e96af;
					border-bottom: 0;

					div.outer {
						border: solid 1px black;
						border-bottom: 0;

						a {
							#gradient > .vertical-four-colors(rgb(121,158,225), rgb(97,126,192), 50%, rgb(27,65,159), 50.1%, rgb(62,157,206));

							.label, .label:hover {
								border: solid 1px rgba(0,0,0,.4);
								border-bottom: 0;
							}

							.menu, .menu:hover, .menu.hover {
								border: solid 1px rgba(0,0,0,.4);
								border-bottom: 0;
								border-top: 0;
							}
						}
					}
				}

					li.active:hover, li.active.hover {
						border: solid 1px #8e96af;
						border-bottom: 0;

						div.outer:hover, div.outer.hover {
							border: solid 1px black;
							border-bottom: 0;

							a:hover, a.hover {
								#gradient > .vertical-four-colors(rgb(94,149,255), rgb(70,109,217), 50%, rgb(19,63,192), 50.1%, rgb(107,211,254));

								.label, .label:hover {
									border: solid 1px rgba(0,0,0,.4);
									border-bottom: 0;
								}

								.menu, .menu:hover, .menu.hover {
									background: none;
									border: solid 1px rgba(0,0,0,.4);
									border-bottom: 0;
									border-top: 0;
									position: relative;

									i {
										visibility: visible;
									}
								}
							}
						}
					}

				li:hover, li.hovertou {
					border: solid 1px transparent;
					border-bottom: 0;

					div.outer {
						border: solid 1px rgba(0,0,0,.8);
						border-bottom: 0;


						a, a:hover {
							#gradient > .vertical-four-colors(rgb(142,152,172), rgb(55,65,91), 50%, rgb(19,30,47), 50.1%, rgb(19,30,47));
							text-decoration: none;

							.label:hover {
								border: solid 1px #bfc4ce;
							}

							.label {
								border: solid 1px #8e98ac;
								border-bottom: 0;
							}

							.menu:hover, .menu.hover {
								border: solid 1px #6983a8;
								background: -moz-radial-gradient(center, ellipse cover, #3778c5 18%, #1a2745 100%); /* FF3.6+ */
								background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(18%,#3778c5), color-stop(100%,#1a2745)); /* Chrome,Safari4+ */
								background: -webkit-radial-gradient(center, ellipse cover, #3778c5 18%,#1a2745 100%); /* Chrome10+,Safari5.1+ */
								background: -o-radial-gradient(center, ellipse cover, #3778c5 18%,#1a2745 100%); /* Opera 12+ */
								background: -ms-radial-gradient(center, ellipse cover, #3778c5 18%,#1a2745 100%); /* IE10+ */
								background: radial-gradient(ellipse at center, #3778c5 18%,#1a2745 100%);

								i {
									visibility: visible;
								}
							}

							.menu {
								background: -moz-radial-gradient(center, ellipse cover, #2e5480 48%, #334258 100%); /* FF3.6+ */
								background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(48%,#2e5480), color-stop(100%,#334258)); /* Chrome,Safari4+ */
								background: -webkit-radial-gradient(center, ellipse cover, #2e5480 48%,#334258 100%); /* Chrome10+,Safari5.1+ */
								background: -o-radial-gradient(center, ellipse cover, #2e5480 48%,#334258 100%); /* Opera 12+ */
								background: -ms-radial-gradient(center, ellipse cover, #2e5480 48%,#334258 100%); /* IE10+ */
								background: radial-gradient(ellipse at center, #2e5480 48%,#334258 100%);
								border: solid 1px #37557f;
							}
						}
					}
				}
		}
	}


